.card {
  width: 20%;
  max-height: 20%;
  margin: 1vw;
}

img {
  width: 100%;
  height: auto;
}

.card-content .card-title {
  max-height: 10vh;
  overflow: scroll;
}
.card-content i.material-icons {
  position: relative;

  top: 0;
  left: 0;
}

i#more_vert.material-icons {
  position: relative;
  top: 0;
  left: 0;
}

@media only screen and (max-width: 950px) {
  .card {
    width: 30%;
  }
}
@media only screen and (max-width: 768px) {
  .card {
    width: 40%;
  }
}
@media only screen and (max-width: 600px) {
  .card {
    width: 100%;
  }
}
